import { createWebHistory, createRouter } from "vue-router";
import Profile from './components/Profile.vue';
import Trigenda from './components/Trigenda.vue';
import RobinHood from './components/RobinHood.vue';
import Music from './components/Music.vue';


const routes = [
  {
    path: "/profile",
    name: "Profile",
    component: Profile,
  },
  {
    path: "/trigenda",
    name: "Trigenda",
    component: Trigenda,
  },
  {
    path: "/robinhood",
    name: "RobinHood",
    component: RobinHood,
  },
  {
    path: "/music",
    name: "Music",
    component: Music,
  },
  {
    path: "/",
    redirect: "/profile",
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;